.carousel-item img {
    height: 75vh;
    object-fit: cover;
    filter: grayscale(60%);
}

.carousel {
    position: relative;
}

.carousel .overlay .container,
.carousel .overlay .row {
    height: 100%;
}

.overlay {
    z-index: 1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
}